input[type="text"] {
  width: 70%;
  height: 40px;
  border: 1px solid #555;
  box-shadow: 3px solid #bbb;
  text-indent: 15px;
  border-radius: 20px 0px 0px 20px;
  margin-left: 60px;
}

input[type="submit"] {
  width: 20%;
  height: 44px;
  box-shadow: 3px solid #bbb;
  border-radius: 0px 20px 20px 0px;
}

section.popup {
  width: 65%;
  padding: 20px;
  background-color: white;
  margin-left: 80px;
  display: none;
}

section.popup::after {
  content: "";
  display: block;
  clear: both;
}

section.popup aside {
  float: left;
  margin-left: 15px;
}
